import './App.css';
import React, {useState, useEffect, Component} from 'react';

import axios from "axios";

class App extends React.Component
{
	constructor(props)
	{
		super(props);

		this.state = {
			message:"hi",
			loading: true,
		};
	}

	componentDidMount() 
	{
		
 		axios.get("/api/name", {headers: {
			'x-api-key': process.env.REACT_APP_API_KEY
		}})
	
		/*
			axios.get("http://localhost:5000/api/name")
			*/
			.then(response =>{
				this.setState({message: response.data.message[0], loading: false});

				console.log(response);
				//console.log(process.env);
		
			})
			.catch(error => {
				console.log(error);	
			})	   		
	
  	}

	render()
	{
		return (
			<div>
			{((this.state.loading)) ? 
				<div>loading...</div> :
				<>
				<div>
		
				<div><h1>Number Value</h1> {this.state.message}</div>



				</div>
				</>
			}
			
		</div>


		
		);
	}
}


